import React, { useState, useEffect } from 'react';
import Fade from 'react-reveal/Fade';
import { Container, Row, Col } from 'react-bootstrap';
import Title from '../Title/Title';
import AboutImg from '../Image/AboutImg';

const About = () => {
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="about">
      <Container>
        <Row className="about-wrapper">
          <Col md={6} sm={12}>
            <Fade bottom duration={500} delay={300} distance="30px">
              <div className="about-wrapper__image">
                <AboutImg alt="profile picture" filename="logo.png" />
              </div>
            </Fade>
          </Col>
          <Col md={6} sm={12}>
            <Fade left={isDesktop} bottom={isMobile} duration={500} delay={500} distance="30px">
              <div className="about-wrapper__info">
                <Title title="Yuki Nishidate" />
                <p className="about-wrapper__info-text">
                  {
                    '慶應義塾大学 理工学部 情報工学科の学部生です。コンピュータグラフィックスに興味があります。'
                  }
                </p>
                <div className="social-links">
                  <a
                    href={'https://twitter.com/asta18425'}
                    rel="noopener noreferrer"
                    target="_blank"
                    aria-label={'twitter'}
                  >
                    <i
                      className={`fa fa-${'twitter' || 'refresh'} fa-inverse`}
                      aria-hidden="true"
                    />
                  </a>
                  <a
                    href={'https://github.com/nishidate-yuki'}
                    rel="noopener noreferrer"
                    target="_blank"
                    aria-label={'github'}
                  >
                    <i className={`fa fa-${'github' || 'refresh'} fa-inverse`} aria-hidden="true" />
                  </a>
                </div>
              </div>
            </Fade>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default About;
