import React, { useContext, useEffect, useState } from 'react';
import Modal from 'react-modal';
import { Row, Col } from 'react-bootstrap';
import Img from 'gatsby-image';

const Post = ({ node }) => {
  const [modalIsOpen, setIsOpen] = useState(false);
  const { title, images, description, date, videos, url } = node;

  const modalStyle = {
    overlay: {
      position: 'fixed',
      top: 0,
      left: 0,
      backgroundColor: 'rgba(0,0,0,0.5)',
    },
    content: {
      position: 'absolute',
      //   top: '15rem',
      //   left: '15rem',
      //   right: '15rem',
      //   bottom: '15rem',
      backgroundColor: 'rgba(255, 255, 255, 1)',
      padding: '15rem',
      maxWidth: '1000px',
      margin: 'auto',
    },
  };

  return (
    <div>
      <button className="post-link-button" onClick={() => setIsOpen(true)}>
        <div className="project-wrapper__post">
          <div className="project-wrapper__image">
            <Img fixed={node.images[0].fixed} />
          </div>
          <div className="project-wrapper__text">
            <Row>
              <Col md={7}>
                <p className="cta-btn text-color-main">{node.title}</p>
              </Col>
              <Col md={5}>
                <p className="date">{node.date}</p>
              </Col>
            </Row>
          </div>
        </div>
      </button>
      <Modal
        isOpen={modalIsOpen}
        style={modalStyle}
        onAfterOpen={() => (document.body.style.overflow = 'hidden')}
        onAfterClose={() => (document.body.style.overflow = 'auto')}
        onRequestClose={() => setIsOpen(false)}
      >
        <Row>
          <Col>
            <h2 className="post-title">{title}</h2>
          </Col>
          <Col>
            <h3 className="post-date">{date}</h3>
          </Col>
        </Row>
        <p className="description">
          {description.description}
          {url && (
            <a
              target="_blank"
              rel="noopener noreferrer"
              className="cta-btn text-color-main link-text"
              href={url}
            >
              [Link]
            </a>
          )}
        </p>

        {images.map((image) => {
          return <Img className="post-image" fluid={image.fluid} />;
        })}
        {videos != null &&
          videos.map((video) => {
            return <video className="post-video" src={video.file.url} controls></video>;
          })}

        {/* <button className="close-button" onClick={() => setIsOpen(false)}>
          <span className="close-span">
            <i className={`fa fa-${'times-circle' || 'refresh'} fa-inverse`} aria-hidden="true" />
          </span>
        </button> */}
      </Modal>
    </div>
  );
};

export default Post;
